"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { useRouter } from "next/navigation"

interface Transaction {
  id: number
  type: "income" | "expense"
  amount: number
  description: string
  date: string
}

export default function FinancePage() {
  const [transactions, setTransactions] = useState<Transaction[]>([])
  const [amount, setAmount] = useState("")
  const [description, setDescription] = useState("")
  const [type, setType] = useState<"income" | "expense">("income")
  const router = useRouter()

  useEffect(() => {
    const user = localStorage.getItem("user")
    if (!user) {
      router.push("/")
    }
  }, [router])

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (amount && description) {
      const newTransaction: Transaction = {
        id: transactions.length + 1,
        type,
        amount: parseFloat(amount),
        description,
        date: new Date().toISOString().split("T")[0]
      }
      setTransactions([...transactions, newTransaction])
      setAmount("")
      setDescription("")
    }
  }

  const totalIncome = transactions
    .filter(t => t.type === "income")
    .reduce((sum, t) => sum + t.amount, 0)

  const totalExpenses = transactions
    .filter(t => t.type === "expense")
    .reduce((sum, t) => sum + t.amount, 0)

  const balance = totalIncome - totalExpenses

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">Control de Finanzas</h2>
      </div>

      <div className="grid md:grid-cols-3 gap-4">
        <Card className="p-6 bg-green-50">
          <h3 className="text-lg font-medium text-green-700">Ingresos Totales</h3>
          <p className="text-2xl font-bold text-green-800">${totalIncome.toFixed(2)}</p>
        </Card>
        <Card className="p-6 bg-red-50">
          <h3 className="text-lg font-medium text-red-700">Gastos Totales</h3>
          <p className="text-2xl font-bold text-red-800">${totalExpenses.toFixed(2)}</p>
        </Card>
        <Card className="p-6 bg-blue-50">
          <h3 className="text-lg font-medium text-blue-700">Balance</h3>
          <p className="text-2xl font-bold text-blue-800">${balance.toFixed(2)}</p>
        </Card>
      </div>

      <div className="grid md:grid-cols-2 gap-6">
        <Card className="p-6">
          <h3 className="text-xl font-semibold mb-4">Registrar Transacci贸n</h3>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="space-y-2">
              <label className="text-sm font-medium">Tipo</label>
              <select
                className="w-full px-3 py-2 border rounded-md"
                value={type}
                onChange={(e) => setType(e.target.value as "income" | "expense")}
              >
                <option value="income">Ingreso</option>
                <option value="expense">Gasto</option>
              </select>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium">Monto</label>
              <Input
                type="number"
                step="0.01"
                placeholder="0.00"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
                required
              />
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium">Descripci贸n</label>
              <Input
                placeholder="Descripci贸n de la transacci贸n"
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                required
              />
            </div>

            <Button type="submit" className="w-full">
              Registrar
            </Button>
          </form>
        </Card>

        <Card className="p-6">
          <h3 className="text-xl font-semibold mb-4">Historial de Transacciones</h3>
          <div className="space-y-4">
            {transactions.map((transaction) => (
              <div
                key={transaction.id}
                className={`p-4 border rounded-lg space-y-2 ${
                  transaction.type === "income" ? "border-green-200" : "border-red-200"
                }`}
              >
                <div className="flex justify-between">
                  <span className="font-medium">${transaction.amount.toFixed(2)}</span>
                  <span className={`text-sm ${
                    transaction.type === "income" ? "text-green-600" : "text-red-600"
                  }`}>
                    {transaction.type === "income" ? "Ingreso" : "Gasto"}
                  </span>
                </div>
                <div className="text-sm text-gray-600">{transaction.description}</div>
                <div className="text-xs text-gray-500">{transaction.date}</div>
              </div>
            ))}
            {transactions.length === 0 && (
              <p className="text-gray-500 text-center">
                No hay transacciones registradas
              </p>
            )}
          </div>
        </Card>
      </div>

      <Card className="p-6">
        <h3 className="text-xl font-semibold mb-4">Reporte Visual</h3>
        <div className="h-64 flex items-center justify-center">
          <div className="flex items-end space-x-4 h-48">
            <div className="flex flex-col items-center">
              <div 
                className="w-20 bg-green-200 rounded-t"
                style={{ height: `${(totalIncome / Math.max(totalIncome, totalExpenses)) * 100}%` }}
              ></div>
              <span className="mt-2 text-sm">Ingresos</span>
            </div>
            <div className="flex flex-col items-center">
              <div 
                className="w-20 bg-red-200 rounded-t"
                style={{ height: `${(totalExpenses / Math.max(totalIncome, totalExpenses)) * 100}%` }}
              ></div>
              <span className="mt-2 text-sm">Gastos</span>
            </div>
          </div>
        </div>
      </Card>
    </div>
  )
}
